.hero-container {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: 1fr 1fr;
  min-height: 800px;
  min-width: 100%;
  padding-top: 100px;
}
@media (max-width: 450px) {
  .hero-container {
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: 1fr;
    min-height: 400px;
    min-width: 100%;
  }
}

@media (max-height: 321px) { 
  .hero-container { 
    padding-top: 0; 
  }
}